.checked {
    text-decoration: line-through;
}
.header{
    font-size: 100px;
    font-weight: 100;
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
    margin: 0;
    padding:0
}
.wrapperDivStyle{
    width: 40%;
    display: flex;
    margin: 32px 0;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 100vh;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);

}

.toDoHeader{
    width: 100%
}

.inputWrapper{
    padding:0 32px;
}
@media only screen and (max-width: 768px) {
    .footerWrapper{
        display: flex;
        flex-direction: column;
    }
}
